#theme-toggle {
  -webkit-appearance: none;
  appearance: none;
  background: #c6c6c6;
  border-radius: 60px;
  outline: none;
  width: 46px;
  height: 20px;
  transition: all .7s;
  position: relative;
  top: 5px;
  box-shadow: 7px 5px 15px #a09ea099;
}

#theme-toggle:checked {
  background: #9bb537;
  box-shadow: 7px 5px 15px #16161699;
}

#theme-toggle:before {
  content: "";
  background: #fff;
  border-radius: 17.5px;
  width: 17.5px;
  height: 17.5px;
  transition: all .5s;
  position: absolute;
  top: .5px;
